<template>
    <p>이 게임은 원하는 만큼의 YAM을 걸고 지뢰 개수를 1개에서 24개까지 선택하면 시작합니다. <br><br>
      5X5 크기의 게임판이 나타납니다. 게임판에는 선택한 개수 만큼의 지뢰가 숨겨져 있습니다. 지뢰가 아닌 타일을 선택하면 보상을 받습니다.
    다음 번에 받을 보상금액은 Next에 나타나 있고 현재 쌓인 금액은 Stake에 나타나 있습니다.
      보상은 건 금액이 클 수록, 그리고 폭탄 개수가 많을수록 커집니다. <br><br>
    쌓인 금액을 돌려받고 싶다면 cashout 버튼을 클릭하면 됩니다.
      쌓인 금액을 cashout하지 않으면 사라져버리니 주의해주시길 바랍니다.
    만약 지뢰를 발견했다면 stake가 0이 되버리고 게임이 종료됩니다.
    </p>
</template>

<script>
    export default {
        name: "BombText"
    }
</script>

<style scoped>
  p {
    margin-top: 0;
    padding-top: 3%;
    padding-left: 55%;
    margin-right: 5vh;
  }
</style>



