    <button>마이페이지</button>
  </div>
</template>

<script>
  import Photo from './Profile/Photo'
  import Name from './Profile/Name'
  import intro from './Profile/intro'

    export default {
        name: "Profile",
      components:{
          Photo,
        Name,
        intro
      }
    }
</script>

<style scoped>
  div {
    margin-top: 5%;
    margin-left: 50px;
    height: 60vh;
    padding: 0;
    border-color: gray;
    border-style: solid;
    border-width: 1px;
    border-radius: 10px;
    background-color: #1f1f1f;
  }
  button {
    margin-left: 6%;
    box-shadow: crimson;
    border:none;
    background-color: white;
    margin-bottom: 5%;
  }
</style>



