<template>
    <p>
      <data id="real">{{ real }}</data>
      <br>
      <br>
      현재 YAM : <data>{{ yam }}</data>
      <br>
      <br>
      랭킹 : <data>25</data>
    </p>
</template>

<script>
    export default {
        name: "intro",
      data() {
          return {
            real : '',
            yam : '',
          }
      },
      beforeCreate :function () {
        axios.get('http://localhost:5000/user/real')
          .then((res)=> {
              console.log(res.data);
              this.real = res.data;
            }
          )

        axios.get('http://localhost:5000/user/yam')
          .then((res)=> {
              console.log(res.data);
              this.yam = res.data;
            }
          )
      },
    }
</script>

<style scoped>
  #real {

    color: white;
  }
  p {
    color: #a07b12;
    text-align: center;
  }
  data {
    color: #a07b12;
  }
</style>



